export default {
	data() {
		return {
			loginType: false,
			isLogin: false, //是否可以登录

			login_code: "", //验证码
			login_photo: "", //手机号

			btnValue: '发送验证码', //验证码文案
			second: 60, //时间s
			btnDisabled: false, //点击状态
		}
	},
	onShow() {

	},
	methods: {
		loginTab() {
			this.loginType = !this.loginType
		},
		isLoginFun() {
			if (this.login_code != "" && this.login_photo != "") {
				this.isLogin = true
			} else {
				this.isLogin = false
			}
		},
		async getCode() {
			//验证手机号
			if (this.blurPhone(this.login_photo)) {
				//防止二次点击
				if (!this.btnDisabled) {
					try {
						let phone = this.login_photo;
						let utc = Date.parse(new Date()) / 1000;
						let secret = "saqqweqw";
						let str = `${phone}${utc}${secret}`
						let sign = this._md5(str)

						let code = await this.$wxhttp.post({
							url: '/lollipop/api/v1/get_vcode',
							data: {
								phone: phone,
								utc: utc,
								sign: sign
							},
						})
						
						this.timer();
						return;

					} catch (error) {
						console.log(error);
					}
				}
			}


		},
		async goLogin() {
			const that = this;
			let phone = "18801287297";
			let password = "123123";
			try {
				let data = await that.$wxhttp.get({
					url: '/api/Auth/Login',
					data: {
						phone: phone,
						password:password
					},
				})
				// if (data.data.access_token) {
				// 	uni.setStorageSync("access_token", data.data);
				// 	uni.navigateBack()
				// }
			} catch (error) {
				console.log(error);
			}

		},
		timer: function() {
			let promise = new Promise((resolve, reject) => {
				let setTimer = setInterval(
					() => {
						var second = this.second - 1;
						this.second = second;
						this.btnValue = second + '秒';
						this.btnDisabled = true;
						if (this.second <= 0) {
							this.second = 60;
							this.btnValue = '获取验证码';
							this.btnDisabled = false;
							resolve(setTimer)
						}
					}, 1000)
			})
			promise.then((setTimer) => {
				clearInterval(setTimer)
			})
		},
		// 手机号验证
		blurPhone: function(phone) {
			let that = this
			var ajxtrue = false;
			if (!(/^1[34578]\d{9}$/.test(phone))) {
				ajxtrue = false;
				uni.showToast({
					title: '手机号有误',
					icon: '/static/img/login_close.png',
					duration: 2000
				})

			} else {
				ajxtrue = true;
			}
			return ajxtrue;
		},

	},

}
